.whatsapp_container{
    width: 100vw;
    height: 100vh;
    background-color: #090e11;
    display: flex;
    align-items: center;
    justify-content: center;
}

.whatsapp_container > .whatsapp_area{
    width: 75vw;
    height: 85vh;
    background-color: #2a2f32;
    display: flex;
}

@media only screen and (max-width: 1400px) {
    .whatsapp_container > .whatsapp_area{
        width: 90vw;
        height: 95vh;
    }
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background-color: #131c21;
}

::-webkit-scrollbar-thumb {
    background-color: #394045;
}

.cabecalho_areamensagem_container{
    width: 100%;
    height: 7.5%;
    background-color: #2a2f32;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    border-left: 1px solid #3f4448;
}

.cabecalho_areamensagem_container > .dados{
    display: flex;
    height: 100%;
    align-items: center;
}

.cabecalho_areamensagem_container > .dados > img{
    border-radius: 50%;
    height: 80%;
    margin-right: 20px;
}

.cabecalho_areamensagem_container > .dados > .dados_info > .nome{
    font-size: 13pt;
    padding-bottom: 7px;
    font-family: sans-serif;
    font-weight: bolder;
    color: #e1e2e3;
}

.cabecalho_areamensagem_container > .dados > .dados_info > .status{
    font-size: 9pt;    
    color: #9c9fa2;
    font-weight: bolder;

}

.cabecalho_areamensagem_container > .icons > svg{
    color: #b1b3b5;
    margin-right: 25px;
}


